.block-title{
	color: $first-color;
	text-align: center;
	@include scm;
	margin: 0 0 50px;
	&.is-left{
		text-align: left;
		margin: 0 0 20px;
	}
	h1{
		font-size: rem(64);
		@include r(1023) {
			font-size: rem(30);
		}
	}

	h2{
		font-size: rem(46);
		@include r(1023) {
			font-size: rem(30);
		}
	}

	h3{
		font-size: rem(28);
		@include r(1023) {
			font-size: rem(22);
		}
	}

	&.is-main{
		h1 {
			color: $first-color;
			font-size: rem(60);
			@include r(1023) {
				font-size: rem(30);
			}
		}
	}

	&.is-short{
		max-width: 800px;
		width: 100%;
	}

	.smaller {
		font-size: rem(34);
	}
}