.header{
	position: fixed;
	z-index: 2;
	width: 100%;
	padding: 20px 0;
	background-color: rgba($second-color, .75);
	@supports(backdrop-filter: blur(8px)) {
		backdrop-filter: blur(8px);
	}
	.navigation{
		&__list{
			@supports(display: grid) {
				grid-template: 1fr / repeat(4, auto);
			}
		}
	}
}