@mixin render($obj){
	@for $i from 1 through 310{
		._#{$i}{
			@if length($obj) >= $i {
				transition-delay: random(450) + ms;
				clip-path: nth($obj, $i);
			} @else {
				clip-path: $void;
			}
		}
	}
}

.word-icon{
	position: fixed;
	z-index: -1;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: calc(4* 25vw);
	height: calc(3* 25vw);
	@include render($random);
	@for $i from 1 through 310{
		._#{$i}{
			background-color: rgba($first-color, random(4)* 0.033);
		}
	}
	&__circle{
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		transition: clip-path .7s cubic-bezier(0.785, 0.135, 0.15, 0.86), opacity 0.5s;
		&:nth-child(3n - 2) {
			animation: opacity 1s;
		}
		&:nth-child(3n - 1) {
			animation: opacity 1.25s;
		}
		&:nth-child(3n) {
			animation: opacity 1.5s;
		}
	}
	&.introduce,
	&.portfolio,
	&.about,
	&.blog{
		.word-icon__circle{
			animation: none;
		}
	}
	&.introduce{
		@include render($swarty);
	}
	&.portfolio{
		@include render($portfolio);
	}
	&.about{
		@include render($about);
	}
}

@keyframes opacity {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}