.case-block{
	max-width: 500px;
	padding: 25px;
	margin-bottom: 150px;
	@include r(1023) {
		margin-bottom: 100px;
	}
	@include r(767) {
		padding: 15px;
		margin-bottom: 0 0 75px;
	}
	

	&.x3{
		max-width: 400px;
		.case-block__picture{
			img{
				width: 100%;
				height: 450px;
				object-fit: cover;
			}
		}
	}

	&__inner{
		background-color: transparent;
		border: 0;
		position: relative;
		padding: 0 0 0 50px;
		&:hover{
			.case-block{
				&__picture{
					box-shadow: 5px 5px 25px rgba($black, .5);
				}
				&__arrow{
					svg{
						transform: translateX(50px);
					}
				}
			}
		}
	}

	&__descr{
		position: absolute;
		bottom: 20px;
		left: 10px;
		z-index: 2;
		max-width: 240px;
		text-align: left;
	}

	&__title{
		font-size: rem(22);
		@include scb;
		margin: 0 0 15px;
		color: $first-color;
	}

	&__counter{
		font-size: rem(16);
		@include scr;
		position: relative;
		padding: 10px 0 0;
		margin: 0 0 5px;
		width: 100%;
		display: block;
		color: rgba($first-color, .5);
		&::after{
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			z-index: 1;
			display: block;
			width: 100%;
			height: 2px;
			background-color: $first-color;
		}
	}

	&__arrow{
		svg{
			transform: translateX(0);
			transition: transform .35s;
			width: 12px;
			height: 12px;
			fill: rgba($first-color, .35);
		}
	}

	&__picture{
		position: relative;
		z-index: 1;
		box-shadow: -10px -10px 15px rgba($black, .5);
		transition: box-shadow .35s ease-in-out;
		&::after{
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			z-index: 1;
			width: 100%;
			height: 100%;
			background: linear-gradient(45deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0.15) 66%, rgba(0,0,0,0) 80%, rgba(0,0,0,0) 100%);
		}

		img{
			height: 250px;
			object-fit: cover;
		}
	}
}