.about{
	.block-wrapper{
		padding: 200px 0 0;
		@include r(1023) {
			padding: 100px 0 0;
		}
		@include r(767) {
			padding: 75px 0 0;
		}
	}

	&__block{
		padding: 0 0 100px;
		margin: 0 0 100px;
		position: relative;
		@include r(1023) {
			margin: 0 0 75px;
			padding: 0 0 75px;
		}
		&::after{
			content: "";
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%);
			width: 200px;
			height: 1px;
			background-color: rgba($thirth-color, .5);
		}
		&:last-of-type{
			&::after{
				display: none;
			}
		}
	}

	&__typography{
		position: relative;
		&::after{
			content: "";
			position: relative;
			display: block;
			clear: both;
		}
		&.media-align-left{
			img{
				float: left;
				margin: 0 25px 25px 0;
			}
		}
		&.media-align-right{
			img{
				float: right;
				margin: 0 0 25px 25px;
			}
		}
		&.media-align-left,
		&.media-align-right{
			img{
				@include r(767) {
					display: block;
					width: 100%;
					float: none;
					margin: 0 0 20px;
				}
			}
		}
		img{
			width: 400px;
			height: auto;
		}
		table{
			width: 100%;
			border-spacing: 1px;
			text-align: left;
			@include r(767) {
				display: block;
				overflow: auto;
			}
		}
		tbody{
			@include r(767) {
				width: 700px;
				display: block;
			}
		}
		td, th {
			padding: 5px 10px;
			width: 33.3333%;
		}
		th{
			color: $thirth-color;
			border: 1px solid $thirth-color;
		}
		td{
			color: $first-color;
			border: 1px solid $first-color;
		}
	}

	&__contacts{
		padding: 100px 0 0;
		margin: 0 0 -50px;
		grid-template-columns: repeat(2, 1fr);
		.block-title{
			grid-column: 1 / 3;
		}
	}
	
	&__contact{
		margin: 0 0 50px;
		a{
			color: $thirth-color;
			grid-template-rows: repeat(2, 1fr);
			justify-items: center;
			&:hover{
				svg{
					fill: $thirth-color;
				}
			}
		}
		span {
			margin: 0 0 10px;
			@include scr;
		}
		svg{
			width: 40px;
			height: 40px;
			fill: $first-color;
			transition: fill .25s;
		}
	}
	&__total-experience{
		margin: 50px 0 0;
		text-align: center;
		span {
			color: $first-color;
		}
	}
	.skills{
		grid-template-columns: auto;
	}
}