.site-button{
	font-size: rem(18);
	width: 160px;
	height: 60px;
	padding: 3px;
	border: 5px dashed $first-color;
	background-color: $second-color;
	position: relative;
	overflow: hidden;
	transition: all .25s;
	&:hover{
		border-style: solid;
	}
	&__text{
		font-size: inherit;
		color: $first-color;
		padding: 0 3px;
		position: relative;
		z-index: 2;
		background-color: inherit;
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
	}
}