.modal{
	&__container{
		grid-template-columns: calc(100vw - m#{i}n(40vw, 600px)) m#{i}n(40vw, 600px);
		height: 100%;
	}

	&__pictures,
	&__content{
		overflow: auto;
	}

	&__pictures{
		img {
			width: 100%;
			height: auto;
		}
	}

	&__content{
		padding: 100px;
		color: $first-color;
		@include r(1365) {
			padding: 100px 50px;
		}
		
		.tags{
			display: flex;
			flex-wrap: wrap;
			margin: 0 0 50px;
			li{
				margin: 0 10px 5px 0;
				position: relative;
				font-size: rem(16);
				text-transform: uppercase;
				@include scm;
				&::before{
					content: "#";
					font: inherit;
				}
			}
		}

		h2{
			margin: 0 0 40px;
		}

		a,
		p{
			@include scm;
			font-size: rem(16);
		}

		p{
			margin: 0 0 20px;
		}

		a{
			@include scb;
			text-decoration: underline;
			&:hover{
				text-decoration: none;
			}
		}
	}

	&__close{
		background-color: transparent;
		border: 0;
		position: absolute;
		top: 0;
		right: 0;
		display: block;
		width: 50px;
		height: 50px;
		&::before,
		&::after{
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 4px;
			background-color: $first-color;
		}
		&::before{
			top: 50%;
			transform: rotate(45deg);
		}
		&::after{
			top: 50%;
			transform: rotate(-45deg);
		}
	}
}