.s-portfolio {
	position: relative;
	padding: 150px 0;
	@include r(1023) {
		padding: 100px 0;
	}
	@include r(767) {
		padding: 75px 0;
	}
	&__modal{
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 3;
		background-color: $second-color;
		pointer-events: none;
		opacity: 0;
		transition: opacity .25s;
		&.is-opened{
			opacity: 1;
			pointer-events: auto;
		}
	}

	.block-title{
		margin: 0 0 150px;
		@include r(1023) {
			margin: 0 0 100px;
		}
		@include r(767) {
			margin: 0 0 50px;
		}
	}

	&__content{
		margin: -25px -25px -150px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: flex-start;
		@include r(1365) {
			justify-content: space-around;
		}
		@include r(767) {
			margin: -15px -15px -100px;
		}
	}
}