html {
  font-size: 18px;
  line-height: 1.35;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'SourceCodePro-Medium', 'Arial', sans-serif;
  font-weight: 400;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

button,
p,
a,
li,
td {
  font-size: 1rem;
}

.typography td,
.typography th,
.typography p,
.typography li {
  font-family: 'SourceCodePro-Regular', 'Arial', sans-serif;
  font-weight: 400;
}

.typography h2,
.typography h3,
.typography h4 {
  padding: 0 0 25px;
}

.typography h2:not(:first-child),
.typography h3:not(:first-child),
.typography h4:not(:first-child) {
  padding-top: 20px;
}

.typography p,
.typography li {
  color: rgba(251, 206, 14, 0.75);
}

.typography li {
  position: relative;
  padding: 0 0 0 20px;
}

.typography li::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 0;
  width: 5px;
  height: 5px;
  background-color: #13C2BD;
  border-radius: 50%;
}

.typography ul,
.typography ol {
  margin: 0 0 20px;
}

.typography p {
  padding: 0 0 25px;
}

.typography p:last-child {
  padding: 0;
}

.typography .headline {
  font-size: 1.22222rem;
  color: #13C2BD;
  padding: 0 0 25px;
  display: inline-block;
}

picture,
img {
  max-width: 100%;
  height: auto;
  display: block;
}

img.lazy {
  background-image: url(data:image/gif;base64,R0lGODlhCgAIAIABAN3d3f///yH5BAEAAAEALAAAAAAKAAgAAAINjAOnyJv2oJOrVXrzKQA7);
}

img.lazy.loaded {
  background-image: none;
}

.site-button {
  font-size: 1rem;
  width: 160px;
  height: 60px;
  padding: 3px;
  border: 5px dashed #FBCE0E;
  background-color: #07143C;
  position: relative;
  overflow: hidden;
  transition: all .25s;
}

.site-button:hover {
  border-style: solid;
}

.site-button__text {
  font-size: inherit;
  color: #FBCE0E;
  padding: 0 3px;
  position: relative;
  z-index: 2;
  background-color: inherit;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  position: relative;
  height: 100%;
}

.wrapper {
  min-height: 100vh;
  position: relative;
  z-index: 1;
  background-color: #07143C;
  overflow: hidden;
}

.main {
  width: 100%;
}

footer {
  margin: auto 0 0;
}

.body-overflow {
  overflow: hidden;
  overscroll-behavior-y: none;
  -webkit-overflow-scrolling: auto;
}

.container {
  max-width: 1050px;
  width: 100%;
  position: relative;
  margin: 0 auto;
  padding: 0 25px;
}

.sr-only {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  width: 1px;
  height: 1px;
  overflow: hidden;
  padding: 0;
  border: 0;
  white-space: nowrap;
}

.contain-picture {
  object-fit: contain !important;
}

a {
  text-decoration: none;
  color: inherit;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

button {
  cursor: pointer;
}

b,
strong {
  font-size: inherit;
  font-weight: inherit;
}

b,
strong {
  font-weight: bolder;
}

ul,
ol {
  list-style: none;
}

i {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

.dc {
  display: flex;
}

@supports (display: grid) {
  .dc {
    display: grid;
    grid-template-columns: minmax(0, 1fr);
  }
}

.dc.vc {
  align-items: center;
}

.dc.hs {
  justify-content: flex-start;
}

.dc.hc {
  justify-content: center;
}

@supports (display: grid) {
  .dc.hc {
    justify-items: center;
  }
}

.dc.he {
  justify-content: flex-end;
}

.dc.wrap {
  display: flex;
  flex-wrap: wrap;
}

.icon,
.icon-arrow,
.icon-cv,
.icon-facebook,
.icon-linkedin,
.icon-mail,
.icon-play-button {
  display: inline-block;
  width: 1em;
  height: 1em;
  fill: currentColor;
}

