@keyframes opacity {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.navigation__item {
  margin: 0 30px 0 0;
  position: relative;
  overflow: hidden;
}

.navigation__item:last-child {
  margin: 0;
}

.navigation__item.is-active a,
.navigation__item.is-active button {
  color: #fbce0e;
}

.navigation__item.is-active a:hover,
.navigation__item.is-active button:hover {
  transform: translateY(0);
}

.navigation__item a,
.navigation__item button {
  position: relative;
  display: inline-block;
  padding: 5px 0;
  color: rgba(251, 206, 14, 0.5);
  font-family: 'SourceCodePro-Regular', 'Arial', sans-serif;
  font-weight: 400;
  font-size: 1rem;
  text-shadow: 0 0 5px rgba(251, 206, 14, 0);
  transition: all .25s;
}

.navigation__item a::before,
.navigation__item button::before {
  content: attr(data-text);
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  display: inline-block;
  padding: 5px 0;
  height: auto;
  transform: translateY(100%);
  text-decoration: inherit;
}

.navigation__item a:hover,
.navigation__item button:hover {
  color: #fbce0e;
  transform: translateY(-100%);
}

.block-title {
  color: #FBCE0E;
  text-align: center;
  font-family: 'SourceCodePro-Medium', 'Arial', sans-serif;
  font-weight: 400;
  margin: 0 0 50px;
}

.block-title.is-left {
  text-align: left;
  margin: 0 0 20px;
}

.block-title h1 {
  font-size: 3.55556rem;
}

.block-title h2 {
  font-size: 2.55556rem;
}

.block-title h3 {
  font-size: 1.55556rem;
}

.block-title.is-main h1 {
  color: #FBCE0E;
  font-size: 3.33333rem;
}

.block-title.is-short {
  max-width: 800px;
  width: 100%;
}

.block-title .smaller {
  font-size: 1.88889rem;
}

.skills {
  margin: 0 -1px -1px 0;
}

.skills__skill {
  padding: 5px;
  border: 1px solid #FBCE0E;
  margin: 0 1px 1px 0;
  font-size: 1.11111rem;
  color: #FBCE0E;
}

.case-block {
  max-width: 500px;
  padding: 25px;
  margin-bottom: 150px;
}

.case-block.x3 {
  max-width: 400px;
}

.case-block.x3 .case-block__picture img {
  width: 100%;
  height: 450px;
  object-fit: cover;
}

.case-block__inner {
  background-color: transparent;
  border: 0;
  position: relative;
  padding: 0 0 0 50px;
}

.case-block__inner:hover .case-block__picture {
  box-shadow: 5px 5px 25px rgba(0, 0, 0, 0.5);
}

.case-block__inner:hover .case-block__arrow svg {
  transform: translateX(50px);
}

.case-block__descr {
  position: absolute;
  bottom: 20px;
  left: 10px;
  z-index: 2;
  max-width: 240px;
  text-align: left;
}

.case-block__title {
  font-size: 1.22222rem;
  font-family: 'SourceCodePro-Bold', 'Arial', sans-serif;
  font-weight: 700;
  margin: 0 0 15px;
  color: #FBCE0E;
}

.case-block__counter {
  font-size: 0.88889rem;
  font-family: 'SourceCodePro-Regular', 'Arial', sans-serif;
  font-weight: 400;
  position: relative;
  padding: 10px 0 0;
  margin: 0 0 5px;
  width: 100%;
  display: block;
  color: rgba(251, 206, 14, 0.5);
}

.case-block__counter::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  display: block;
  width: 100%;
  height: 2px;
  background-color: #FBCE0E;
}

.case-block__arrow svg {
  transform: translateX(0);
  transition: transform .35s;
  width: 12px;
  height: 12px;
  fill: rgba(251, 206, 14, 0.35);
}

.case-block__picture {
  position: relative;
  z-index: 1;
  box-shadow: -10px -10px 15px rgba(0, 0, 0, 0.5);
  transition: box-shadow .35s ease-in-out;
}

.case-block__picture::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, black 0%, rgba(0, 0, 0, 0.15) 66%, rgba(0, 0, 0, 0) 80%, rgba(0, 0, 0, 0) 100%);
}

.case-block__picture img {
  height: 250px;
  object-fit: cover;
}

.modal__container {
  grid-template-columns: calc(100vw - min(40vw, 600px)) min(40vw, 600px);
  height: 100%;
}

.modal__pictures,
.modal__content {
  overflow: auto;
}

.modal__pictures img {
  width: 100%;
  height: auto;
}

.modal__content {
  padding: 100px;
  color: #FBCE0E;
}

.modal__content .tags {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 50px;
}

.modal__content .tags li {
  margin: 0 10px 5px 0;
  position: relative;
  font-size: 0.88889rem;
  text-transform: uppercase;
  font-family: 'SourceCodePro-Medium', 'Arial', sans-serif;
  font-weight: 400;
}

.modal__content .tags li::before {
  content: "#";
  font: inherit;
}

.modal__content h2 {
  margin: 0 0 40px;
}

.modal__content a,
.modal__content p {
  font-family: 'SourceCodePro-Medium', 'Arial', sans-serif;
  font-weight: 400;
  font-size: 0.88889rem;
}

.modal__content p {
  margin: 0 0 20px;
}

.modal__content a {
  font-family: 'SourceCodePro-Bold', 'Arial', sans-serif;
  font-weight: 700;
  text-decoration: underline;
}

.modal__content a:hover {
  text-decoration: none;
}

.modal__close {
  background-color: transparent;
  border: 0;
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 50px;
  height: 50px;
}

.modal__close::before,
.modal__close::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #FBCE0E;
}

.modal__close::before {
  top: 50%;
  transform: rotate(45deg);
}

.modal__close::after {
  top: 50%;
  transform: rotate(-45deg);
}

.header {
  position: fixed;
  z-index: 2;
  width: 100%;
  padding: 20px 0;
  background-color: rgba(7, 20, 60, 0.75);
}

@supports (backdrop-filter: blur(8px)) {
  .header {
    backdrop-filter: blur(8px);
  }
}

@supports (display: grid) {
  .header .navigation__list {
    grid-template: 1fr/repeat(4, auto);
  }
}

.s-introduce {
  overflow: hidden;
  width: 100%;
  position: relative;
}

@supports (min-height: 100vh) {
  .s-introduce {
    min-height: 100vh;
    height: auto;
  }
}

.s-introduce .block-wrapper {
  min-height: 100vh;
}

.s-introduce .block-title {
  margin: 0;
}

.about .block-wrapper {
  padding: 200px 0 0;
}

.about__block {
  padding: 0 0 100px;
  margin: 0 0 100px;
  position: relative;
}

.about__block::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 200px;
  height: 1px;
  background-color: rgba(19, 194, 189, 0.5);
}

.about__block:last-of-type::after {
  display: none;
}

.about__typography {
  position: relative;
}

.about__typography::after {
  content: "";
  position: relative;
  display: block;
  clear: both;
}

.about__typography.media-align-left img {
  float: left;
  margin: 0 25px 25px 0;
}

.about__typography.media-align-right img {
  float: right;
  margin: 0 0 25px 25px;
}

.about__typography img {
  width: 400px;
  height: auto;
}

.about__typography table {
  width: 100%;
  border-spacing: 1px;
  text-align: left;
}

.about__typography td,
.about__typography th {
  padding: 5px 10px;
  width: 33.3333%;
}

.about__typography th {
  color: #13C2BD;
  border: 1px solid #13C2BD;
}

.about__typography td {
  color: #FBCE0E;
  border: 1px solid #FBCE0E;
}

.about__contacts {
  padding: 100px 0 0;
  margin: 0 0 -50px;
  grid-template-columns: repeat(2, 1fr);
}

.about__contacts .block-title {
  grid-column: 1 / 3;
}

.about__contact {
  margin: 0 0 50px;
}

.about__contact a {
  color: #13C2BD;
  grid-template-rows: repeat(2, 1fr);
  justify-items: center;
}

.about__contact a:hover svg {
  fill: #13C2BD;
}

.about__contact span {
  margin: 0 0 10px;
  font-family: 'SourceCodePro-Regular', 'Arial', sans-serif;
  font-weight: 400;
}

.about__contact svg {
  width: 40px;
  height: 40px;
  fill: #FBCE0E;
  transition: fill .25s;
}

.about__total-experience {
  margin: 50px 0 0;
  text-align: center;
}

.about__total-experience span {
  color: #FBCE0E;
}

.about .skills {
  grid-template-columns: auto;
}

.s-portfolio {
  position: relative;
  padding: 150px 0;
}

.s-portfolio__modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 3;
  background-color: #07143C;
  pointer-events: none;
  opacity: 0;
  transition: opacity .25s;
}

.s-portfolio__modal.is-opened {
  opacity: 1;
  pointer-events: auto;
}

.s-portfolio .block-title {
  margin: 0 0 150px;
}

.s-portfolio__content {
  margin: -25px -25px -150px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}

.footer {
  background-color: #1e1f1f;
}

.footer__body {
  padding: 5px 0;
  display: flex;
  justify-content: space-between;
}

.footer__body p {
  color: #FBCE0E;
  font-size: 0.88889rem;
  font-family: 'SourceCodePro-ExtraLight', 'Arial', sans-serif;
  font-weight: 300;
}

@media all and (max-width: 1365px) {
  .modal__content {
    padding: 100px 50px;
  }

  .s-portfolio__content {
    justify-content: space-around;
  }
}

@media all and (max-width: 1023px) {
  .navigation__item a,
  .navigation__item button {
    font-size: 0.88889rem;
  }

  .block-title h1 {
    font-size: 1.66667rem;
  }

  .block-title h2 {
    font-size: 1.66667rem;
  }

  .block-title h3 {
    font-size: 1.22222rem;
  }

  .block-title.is-main h1 {
    font-size: 1.66667rem;
  }

  .case-block {
    margin-bottom: 100px;
  }

  .about .block-wrapper {
    padding: 100px 0 0;
  }

  .about__block {
    margin: 0 0 75px;
    padding: 0 0 75px;
  }

  .s-portfolio {
    padding: 100px 0;
  }

  .s-portfolio .block-title {
    margin: 0 0 100px;
  }
}

@media all and (max-width: 767px) {
  .container {
    padding: 0 15px;
  }

  .case-block {
    padding: 15px;
    margin-bottom: 0 0 75px;
  }

  .about .block-wrapper {
    padding: 75px 0 0;
  }

  .about__typography.media-align-left img,
  .about__typography.media-align-right img {
    display: block;
    width: 100%;
    float: none;
    margin: 0 0 20px;
  }

  .about__typography table {
    display: block;
    overflow: auto;
  }

  .about__typography tbody {
    width: 700px;
    display: block;
  }

  .s-portfolio {
    padding: 75px 0;
  }

  .s-portfolio .block-title {
    margin: 0 0 50px;
  }

  .s-portfolio__content {
    margin: -15px -15px -100px;
  }
}