@include font-face('SourceCodePro-ExtraLight', '../fonts/');
@include font-face('SourceCodePro-Light', '../fonts/');
@include font-face('SourceCodePro-Regular', '../fonts/');
@include font-face('SourceCodePro-Medium', '../fonts/');
@include font-face('SourceCodePro-SemiBold', '../fonts/');
@include font-face('SourceCodePro-Bold', '../fonts/');
@include font-face('SourceCodePro-Black', '../fonts/');

@mixin scel{
	font-family: 'SourceCodePro-ExtraLight', 'Arial', sans-serif;
	font-weight: 300;
}

@mixin scl{
	font-family: 'SourceCodePro-Light', 'Arial', sans-serif;
	font-weight: 300;
}

@mixin scr{
	font-family: 'SourceCodePro-Regular', 'Arial', sans-serif;
	font-weight: 400;
}

@mixin scm{
	font-family: 'SourceCodePro-Medium', 'Arial', sans-serif;
	font-weight: 400;
}

@mixin scsb{
	font-family: 'SourceCodePro-SemiBold', 'Arial', sans-serif;
	font-weight: 600;
}

@mixin scb{
	font-family: 'SourceCodePro-Bold', 'Arial', sans-serif;
	font-weight: 700;
}

@mixin scbl{
	font-family: 'SourceCodePro-Black', 'Arial', sans-serif;
	font-weight: 800;
}


html{
	font-size: 18px;
	line-height: 1.35;
}

h1,h2,h3,h4,h5,h6{
	@include scm;
}

h1,h2,h3,h4,h5,h6,p{
	margin: 0;
	padding: 0;
}


button, p, a, li, td{
	font-size: rem(18);
}


.typography {
	td,th,p,li{
		@include scr;
	}

	h2,
	h3,
	h4{
		padding: 0 0 25px;
		&:not(:first-child){
			padding-top: 20px;
		}
	}

	p, li{
		color: rgba($first-color, .75);
	}

	li{
		position: relative;
		padding: 0 0 0 20px;
		&::before{
			content: "";
			position: absolute;
			top: 10px;
			left: 0;
			width: 5px;
			height: 5px;
			background-color: $thirth-color;
			border-radius: 50%;
		}
	}

	ul, ol{
		margin: 0 0 20px;
	}
	
	p {
		padding: 0 0 25px;
		&:last-child{
			padding: 0;
		}
	}

	.headline{
		font-size: rem(22);
		color: $thirth-color;
		padding: 0 0 25px;
		display: inline-block;
	}
}


picture,
img{
	max-width: 100%;
	height: auto;
	display: block;
}


img{
	&.lazy{
		background-image: url(data:image/gif;base64,R0lGODlhCgAIAIABAN3d3f///yH5BAEAAAEALAAAAAAKAAgAAAINjAOnyJv2oJOrVXrzKQA7);
		&.loaded{
			background-image: none;
		}
	}
}