.navigation{
	&__item{
		margin: 0 30px 0 0;
		position: relative;
		overflow: hidden;
		&:last-child{
			margin: 0;
		}

		&.is-active{
			a,
			button {
				color: rgba($first-color, 1);
				&:hover{
					transform: translateY(0);
				}
			}
		}

		a,
		button{
			position: relative;
			display: inline-block;
			padding: 5px 0;
			color: rgba($first-color, .5);
			@include scr;
			font-size: rem(18);
			text-shadow: 0 0 5px rgba($first-color, 0);
			transition: all .25s;
			@include r(1023) {
				font-size: rem(16);
			}
			&::before{
				content: attr(data-text);
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				display: inline-block;
				padding: 5px 0;
				height: auto;
				transform: translateY(100%);
				text-decoration: inherit;
			}
			&:hover{
				color: rgba($first-color, 1);
				transform: translateY(-100%);
			}
		}
	}
}